import {mapState} from 'vuex';
import many from '@/components/assets/userspic/many.vue'
import f from "@/application/functions";
import _ from "underscore";

export default {
  name: 'userspic',
  props: {
    users: Array,
    unseen: Number,
    slidesPerView: Number,
    chat: {},
    m_chat: {}
  },

  data: function () {
    return {
      convertedUsers: []
    }
  },

  components: {
    many
  },

  created: () => {

  },

  computed: mapState({
    log: function () {
    },
    auth: state => state.auth,
    info: state => {

    },
    roomMembers: function () {
      var pUsers = this.$store.state.users
      var self = this
      var my = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      if (this.m_chat._selfMembership === 'invite') {
        var names = _.map(self.chat.members, function (member) {
          return member.name
        })

        this.core.user.usersInfo(names).then(r => {
          this.convertedUsers = _.map(r, function (user) {
            if (user.id !== my) {
              var data = {}
              _.map(self.chat.members, function (member) {
                if ((self.chat.members.length > 2) && member.name === user.id) {
                  return data = {
                    id: user.id,
                    name: user.name,
                    image: user.image,
                    status: member.status,
                    source: user.source
                  }
                }
                if((self.chat.members.length === 2) && user.id !== my && member.name === user.id){
                   return data = {
                    id: user.id,
                    name: user.name,
                    image: user.image,
                    status: member.status,
                    source: user.source
                  }
                }

              })
              return data
            }
          }).filter(n => n)
        }).catch(e => {
          console.log(e, "eeerror name")
        })
      }
      if (this.m_chat._selfMembership === 'join') {
        this.convertedUsers = _.map(self.chat.members, function (member) {
          var userID = f.getmatrixid(member.userId)
          if((self.chat.members.length > 2)){
             return {
              id: userID,
              status: member.membership,
              name: pUsers[userID].name,
              image: pUsers[userID].image,
              source: pUsers[userID].source
            }
          }
          if((self.chat.members.length === 2) && userID !== my){
             return {
              id: userID,
              status: member.membership,
              name: pUsers[userID].name,
              image: pUsers[userID].image,
              source: pUsers[userID].source
            }
          }

        }).filter(n => n)
      // console.log(this.convertedUsers, "Test")
      }
      return this.chat.members
    }
  }),

  methods: {},
}
